import React, { Component } from 'react'
import './index.less'
import songImg from '../../static/back2.png'
import songImg2 from '../../static/song.png'
import songImg3 from "../../static/images.jpg"
// 引入时间轴组件
import Expirence from "../expirence/index.jsx"
import Buttons from '../button/index.jsx'
// 引入自己的模块
import song from '../../song.js'


export default class Index extends Component {
  constructor() {
    super();
    this.state = {
      isShowMeun: false,//是否展示meun
      meunActive:1,//当前展示的哪个
      spec:90,//滚动初始距离
      meunList:[
        {name:"首页",target:"readme"},
        {name:"我的介绍",target:"aboutme"},
        {name:"部分作品",target:'myprojects'},
        {name:"和我联系",target:"experience"},
        {name:"个人博客",target:"myui"}]

    }
    this.showMeun = this.showMeun.bind(this);
    this.goto=this.goto.bind(this)
  }
  componentDidMount() {
    let model = new song.MasonryLayt('showprojects', 0);
    window.onload = function () {
      model.initLayt();
    }
    // 滚动监听：
    window.onscroll=()=>{
      // 监听滚动距离
      let {meunList}=this.state;
      meunList.forEach((item,index)=>{
        let top=document.getElementsByClassName(item.target)[0].getBoundingClientRect().top;
        let bottom=document.getElementsByClassName(item.target)[0].getBoundingClientRect().bottom;
        if(bottom>0&&top<this.state.spec&&top>0||index==0&&top==0) {
          this.setState({
            meunActive:index
          })
        }
      })
    }
    window.onresize = function () {
      model.initLayt();
      
    }
  }
  showMeun() {
    console.log('dg')
    this.setState({
      isShowMeun: !this.state.isShowMeun
    })
  }
  toDown(){
    console.log('下去')
    song.Slide.scrollTo("aboutme",this.state.spec)
  }
  goto(target,index){
    song.Slide.scrollTo(target,this.state.spec)
    console.log(target)
    this.setState({
      meunActive:index
    })
    this.showMeun();

  }

  render() {
    return (
      <div className="index">
        
        {/* <div className="bounce">ha</div> */}
        {/* 页面设计最外面分两个板块，头部菜单板块，内容板块，底部板块 */}
        <div className="tabNav">
          <div className="title">
            <div className="top">
              <div></div>
              <div>SC.lucky</div>
            </div>
            <div className="bottom">
              Hi,my name is SC
              </div>
          </div>
          <div className={`meun_btn ${this.state.isShowMeun ? "active" : ""}`} onClick={this.showMeun}>
            <span></span><span></span><span></span>
          </div>
          {/* 手机菜单 */}
        <div className={`meunList ${this.state.isShowMeun ? "nav_active" : ''}`}>
          <div className={`content `}>
            {this.state.meunList.map((item,index)=>{
              return(
              <div onClick={()=>{
                this.goto(item.target,index)
              }}
                className={`item ${index==this.state.meunActive?'active':''}`} 
                key={index}>{item.name}
                
              </div>)
            })}

          </div>
        </div>
        </div>
        
        <div className="showContent">
          <div className="readme">
            <div className="back phone">
              <div className="zheBack"></div>
              <img className="img" src={songImg3} alt="" />
              <div className="zheDesc">
                <div>人成各，今非昨，病魂常似秋千索</div>
                <div>——唐婉《钗头凤》</div>
              </div>
              
            </div>
            <img className="bounce" onClick={this.toDown} src={require('../../static/down.png').default} alt=""/>
            <div className="mycard pc">
              <div className="radiol radiol1"></div>
              <div className="radiol radiol2"></div>
              <div className="radiol radiol3"></div>
              <div className="top">
                <div className="left">
                  <img src={require('../../static/1.svg').default} alt=""/>
                  <div className="middle" data-word="大雪过后，一定立春">
                  大雪过后，一定立春
                  <div className="white"></div>
              </div>
                </div>
                <span className="right">简历</span>
              </div>
              

              <div className="bottom">
                <div>APP</div>
                <div>taro</div>
                <div>h5+d3</div>
                <div>uni-app</div>
                <div>微信小程序</div>
              </div>
            </div>
          </div>
          {/* 关于我 */}
          <div className="aboutme item">
            <div className="logo">
              <div className="top">
                <span >About </span>
                <span style={{ color: "orange" }}>Me</span>
              </div>
              <div className="bottom">
                <span></span>
                <span>我的介绍</span>
                <span></span>
              </div>
            </div>
            <div className="myhead">
              <div className="back"></div>
              <img src={songImg2} alt="" />
            </div>
            <div className="content">
              <div style={{ color: "orange" }}>遥怜小儿女，未解忆长安</div>
              <div>我毕业于内江师范学院软件工程专业，2020年6月毕业，在大学四年的深造中，我发现自己更热衷于
              创造表意丰富的网站，因而渐渐从后台转入前端，在工作将近一年的时间里，我不断地丰富自己的羽翼，
              学习ui设计和ux设计，并加强自己的原生js编程能力，后台转前端是一个很艰难的过程，
              前端这个行业太过火热，模块化开发造轮子日益更新，需要不断地发现自己的漏洞，不断地去学习。
              </div>
            </div>
          </div>
          {/* 我的项目 */}
          <div className="myprojects item">
            <div className="logo">
              <div className="top">
                <span >My </span>
                <span style={{ color: "orange" }}>Portfolio</span>
              </div>
              <div className="bottom">
                <span></span>
                <span>我的作品</span>
                <span></span>
              </div>
            </div>
            <div className="content showprojects">
              <img src={require('../../static/back.png').default} alt="" />
              <img src={require('../../static/song.png').default} alt="" />
              <img src={require('../../static/1 (7).jpeg').default} alt="" />
              <img src={require('../../static/china3.png').default} alt="" />

              <img src={require('../../static/1 (8).jpeg').default} alt="" />
              <img src={require('../../static/1 (9).jpeg').default} alt="" />
            </div>
          </div>
          <div className="experience item">
            <div className="logo">
              <div className="top">
                <span >My </span>
                <span style={{ color: "orange" }}>Experience</span>
              </div>
              <div className="bottom">
                <span></span>
                <span>工作经验</span>
                <span></span>
              </div>
            </div>
            <Expirence></Expirence>
          </div>
          <div className="myui item">
            <div className="logo">
              <div className="top">
                <span >My </span>
                <span style={{ color: "orange" }}>Moldule</span>
              </div>
              <div className="bottom">
                <span></span>
                <span>模块分享</span>
                <span></span>
              </div>
            </div>
            <div>
              之前画了点时间学习ux，自己尝试着做了些优化用户体验的各种动效，并自己写了一些ui，如有兴趣可以点击下方按钮，前往查看预览。
              之前还做了个在线css编辑，写页面在调整样式的时候会花费太多时间，因此自己写了个css样式编辑器，有兴趣的可以看看。
              瀑布流模块
              流加载模块
              懒加载模块
              <Buttons></Buttons>
            </div>
          </div>
        </div>
      </div>
    )
  }
  
}