import React from 'react';
import song from '../../song';
import './index.less'
export default class Expirence extends React.Component {
  constructor() {
    super();
  }
  componentDidMount(){
    var items = document.querySelectorAll(".timeline li");

    let lazyModel=new song.LazyLoad(items,function(e){
      if(!e.classList.contains("in-view")){
        e.classList.add("in-view");
      }
    },function(e){
      e.classList.remove("in-view");
    })

    window.addEventListener("load", lazyModel.control.bind(lazyModel));
    window.addEventListener("scroll", lazyModel.control.bind(lazyModel));
  }
  render() {
    return (
      <div className="index">
        <section className="timeline">
          <ul>
            <li>
              <div className="lineContent">
                <div>
                再过几天，章史就要结婚了，为了让章史看见自己，萤重新变成了萤火虫的形态---这么做的话，它就要在一天后死亡，但它还是义无反顾。章史与未婚妻在散步的时候，萤飞到了他们面前，并且带了成千只萤火虫，璀璨如繁星……－－《夏目友人帐》
                </div>
                <div className="san"></div>
              </div>
            </li>
            <li>
              <div className="lineContent">
                <div>“你和萤火虫有两个共同点，在我的眼里都会发光，同时，都已经很多年没见了。”</div>
                <div className="san"></div>
              </div>
            </li>
            <li>
              <div className="lineContent">
                <div>刚简单的算了一下，符合条件的日期共有1080天。首先日期格式是这样ABCX . CB .A 且C=0或1，当C=0时:1≤B≤9，C等于1时:B=0或1或2。1≤A≤9。0≤X≤9。9×9×10+9×3×10=1080。也就是说在这9999年中有1080天的日期都可以正着反着读起来都是当天，最早的是1010年10月1日，最后一个是9909年9月9日。</div>
                <div className="san"></div>
              </div>
            </li>
          </ul>
        </section>
      </div>
    )
  }

}