class MasonryLayt{
  constructor(className,gap){
    this.box=document.getElementsByClassName(className)[0];
    this.children=this.box.children;//获取直接子元素
    this.gap=gap;
  }
  initLayt(type){//排列
      // 若是瀑布流，那么外壳必须设置定位,内部元素设置绝对定位
      if(this.box.style.position!="relative") this.box.style.position="relative";
      for(let i=0;i<this.children.length;i++){
        if(this.children[i].style.position!="absolute") this.children[i].style.position='absolute'
      }
      let children=this.children
      // let pageWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;//当前页面宽度（注意兼容
      let pageWidth=this.box.offsetWidth;
      let childrenWidth = children[0].offsetWidth;//子元素宽度(实现瀑布流时，子元素宽度必须固定)
      let columns = parseInt(pageWidth / (childrenWidth + this.gap));//排列数
      let arr = [];
      children.prototype=Array.prototype.map
      for (let i = 0; i < children.length; i++) {
        if (i <columns) {
          // 先确定第一行
          children[i].style.top = 0;
          children[i].style.left = (childrenWidth + this.gap) * i + 'px';
          arr.push(children[i].offsetHeight);
          // 获取最大值
        } else {
          // 在其他行中，确定当前列的最小高度以及索引
          let minHeight = arr[0];
          let index = 0;
          for (let j = 0; j < arr.length; j++) {
            if (minHeight > arr[j]) {
              minHeight = arr[j];
              index = j;
            }
          }
          // 将下一行第一个盒子的位置，设置在上一行高度最矮的后面，并更新当前列的最小高度，最小列的高度 = 当前自己的高度 + 拼接过来的高度 + 间隙的高度
          children[i].style.top = arr[index] + this.gap + 'px';
          children[i].style.left = children[index].offsetLeft + 'px';
          arr[index] = arr[index] + children[i].offsetHeight + this.gap;
        }
      }
      // 由于定位，让子元素脱离文档，父级高度坍塌，需要加上
      this.box.style.height=Math.max(...arr)+"px"
      
  }
}
/**
 * 构建懒加载模块
 * 初始化需要懒加载的对象
 * 针对流加载，懒加载还需要提供添加对象的方法
 * 创建对象后，让开发者去实时触发control（比如定时器或者页面滚动。
 * 1.lazeList需要懒加载处理的节点
 * 2.entercallback进入页面回调
 * 3.outercallback离开页面回调
 * 4.enterinstance 进入初始距离,默认100
 * 5.outerinstance 离开初始距离，默认0
*/
class LazyLoad{
  constructor(lazyList,enterCallback,outerCallback,enterInstance,outerInstance){
    
    this.lazyList=lazyList;
    
    this.enterCallback=enterCallback;
    this.outerCallback=outerCallback;
    this.enterInstance=-100;
    this.outerInstance=100;
    if(enterInstance) this.enterInstance=enterInstance;
    if(outerInstance) this.outerInstance=outerInstance;
  }
  //触发器，优化：进入和离开只能执行一次，因为节点实质也是对象，可以增加属性isLoad
  // isLoad:不存在——没有加载，0——已离开，1——已加载
  control(){
    let lazyList=this.lazyList;
    for(let i=0;i<lazyList.length;i++){
      let bound=lazyList[i].getBoundingClientRect();
      let clientHeight=window.innerHeight;
      if((bound.top<=clientHeight+this.enterInstance)&&(bound.bottom>0)) if(!lazyList[i].isLoad){
        this.enterCallback(lazyList[i]);
        lazyList[i].isLoad=1
      }
      if(bound.bottom<this.outerInstance) if(lazyList[i].isLoad==1){
        this.outerCallback(lazyList[i]);
        lazyList[i].isLoad=0;
      }
      if(bound.top>clientHeight+this.enterInstance) if(lazyList[i].isLoad==1){
        this.outerCallback(lazyList[i]);
        lazyList[i].isLoad=0;
      }
    }
  }
  // 加入监听对象
  addLazyList(lazyList){
    this.lazyList.push(...lazyList)
  }
}


// className:class名，spec初始量
// 调用静态方法scrollTo即可,不用实例化对象
class Slide{
  static scrollTo(className,spec){
    let top=document.getElementsByClassName(className)[0].offsetTop;
    let left=document.getElementsByClassName(className)[0].offsetLeft;
    // 偏移量
    window.scrollTo({
      top:top-spec,
      left:left,
      behavior:"smooth"
    })
  }
}

export default{
  MasonryLayt:MasonryLayt,
  LazyLoad:LazyLoad,
  Slide:Slide
}
